body {
  font-size: 1.6rem;
  font-family: var(--primary-font);
  background-color: hsl(0, 0%, 90%);
}

.container {
  width: 100%;
  margin: 0 auto;
}

.navbar {
  display: flex;
  justify-content: space-between;
  padding: 1em;
}
.navbar__logo {
  padding: 0.5em;
  display: flex;
  align-items: center;
  gap: 0.25em;
  font-family: var(--primary-font-light);
  font-size: 3.2rem;
}
.navbar__logo-icon {
  width: 25px;
}
.navbar__burger-icon {
  padding: 1em;
  background: none;
  border: 2px solid transparent;
  border-radius: 8px;
  font-size: 1.6rem;
  cursor: pointer;
}
.navbar__burger-icon-line {
  width: 40px;
  height: 4px;
  background-color: #000;
  border-radius: 2px;
}
.navbar__burger-icon-line:first-child {
  margin-bottom: 8px;
}
.navbar__burger-icon-line:last-child {
  margin-top: 8px;
}
.navbar__burger-icon:focus {
  outline: none;
  border-color: #000;
}
.navbar__links {
  display: none;
}

@media screen and (min-width: 992px) {
  .container {
    width: 960px;
  }
}